.board__container {
  display: grid;
  height: 100%;
  grid-template-rows: 3em 4em 1fr auto auto;
  position: relative;
}

.object__container {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Tablets and Beyond*/
@media (min-width: 480px){
  .board__container{
    grid-template-rows: 1fr 6fr 1fr;
    grid-template-columns: 2fr 6fr 2fr;
    column-gap: 1vmax;
    margin-right: 1em;
    margin-left: 1em;
  }
  .scores__container { 
    grid-column: span 3;
    order: -1;
  }
  .feedback__container {
    grid-column: span 3;
    order: 3;
  }
  .object__container {
    border: 1px solid lightgray;
    border-radius: 50px;
    order: 1;
  }
  .timer__container {
    border: 1px solid lightgray;
    border-radius: 50px;
    order: 2;
  }
  .tries__container {
    border: 1px solid lightgray;
    border-radius: 50px;
    order: 0;
  }
}



